body, html {
  background-color: #F8FDF8;
  font-family: 'Amatic SC', cursive;
  margin: 0;
  min-height: 100%;
  padding: 0;
  height: 100%;
}


.navigation-container {
  -ms-flex-align: end;
      align-items: flex-end;
  background-color: #F8FDF8;
  background-image: url('https://spartanburghumane.org/wp-content/uploads/2016/11/black-lab-lying-down-looking-up-opt.png');
  background-position: left;
  background-repeat: no-repeat;
  background-size: contain;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  height: 160px;
  width: 100%;
}

h1 {
  font-size: 55px;
  font-weight: bold;
  margin-right: 20px;
  text-align: right;
}

.nav-link {
  color: #253848;
  text-decoration: none;
}

.nav-link:hover {
  color: #954856;
  text-decoration: underline;
}



.navigations {
  -ms-flex-align: end;
      align-items: flex-end;
  color: #253848;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  font-size: 35px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 10px 20px;
  text-align: right;
  width: 45%;
}

.search-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  font-size: 24px;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 15px;
}



